@import url(https://fonts.googleapis.com/css?family=Raleway);
@import url(https://fonts.googleapis.com/css?family=Raleway);
body{
	overflow-x: hidden;
	background-color: rgba(30,30,30,1);
	max-width: 100vw;
}

.style_header__3gi06 {
    text-align: center;
    padding: 4rem;
}

.style_title__1yeMR {
    color: aliceblue;
    font-family: 'Lemon Milk';
    font-size: 3rem;
    font-weight: 700;
}

.style_white-bg__V3LbE {
	position: fixed;
	top: -10vh;
	z-index: 0;
	bottom: 0;
	width: 100%;
	background-color: ghostwhite;
}

h1{
	position: relative;
}
.style_container__1s1P0{
	display: grid;
    grid-template-columns: auto auto auto auto;
    grid-row-gap: 5vw;
    grid-column-gap: 5px;
    z-index: 2;
    margin: 10vh 10vw;
    justify-items: center;
}

@media screen and (max-width:1400px){
	.style_container__1s1P0{
		grid-template-columns: auto auto auto;
		margin: 0vh 12.5vw;
		margin-bottom: 5vh;
	}
}
@media screen and (max-width:1100px){
	.style_container__1s1P0{
		grid-template-columns: auto auto;
		margin: 0vh 15vw;
	}
	.style_title__1yeMR {
	font-size: 2.5rem;
}
}

/*ipad potrait*/
@media screen and (max-width:800px) and (max-height: 1200px ){
	.style_container__1s1P0{
		grid-template-columns: auto auto;
		margin: 0vh 9vw;
	}
}

@media screen and (max-width:580px){
	.style_container__1s1P0{
		grid-template-columns: auto;
		margin: 0vh 11vw;
	}
}
.style_card__6QdsP{
	position: relative;
	height:17.5rem;
	width:17.5rem;
	background-color: black;
	/* border-radius: 10px; */
	z-index: 0;
        -webkit-transition: box-shadow 0.5s, -webkit-transform 0.5s;
        transition: box-shadow 0.5s, -webkit-transform 0.5s;
        transition: box-shadow 0.5s, transform 0.5s;
        transition: box-shadow 0.5s, transform 0.5s, -webkit-transform 0.5s;
        word-break: break-all !important;
        
}

.style_card__6QdsP img{
	position: relative;
	height:17.5rem;
	width:17.5rem;
	/* border-radius: 10px; */
	background-size: cover;
	background-repeat: no-repeat;
	background-position: 50% 50%;
}

.style_below-card__UEnxt{
        font-family: 'Montserrat', sans-serif;
	margin-top: 15px;
	color: white;
}

.style_contact-overlay__mVTgc {
	position: absolute;
	top: 0;
	bottom: 0;
	height: inherit;
	width: inherit;
	opacity: 0;
	-webkit-transition: .5s ease;
	transition: .5s ease;
	background-color:rgba(0,0,0,0.8);
  }

  .style_card__6QdsP:hover .style_contact-overlay__mVTgc {
	opacity: 1;
  }
  
  .style_card-info__6mD0x{
	position: absolute;
	left: 0;
	margin: 0 auto;
	padding: 0 10px;
	bottom:3.5vh;
	color:white;
	font-style: 16px;
	line-height: 24px;
	z-index: 20;
        opacity:1;
        font-family: 'Montserrat', sans-serif;
}

  .style_card-info__6mD0x img{
	height: 15px;
	width: auto;
	border-radius: 0;
	border-radius: initial;
}
a{
	text-decoration: none;
	color:white;
}

.style_contact-social__2aUfu {
        padding-top: 10px;
        width:25px !important;
        height:25px !important;
}

.style_name__3masB{
        font-weight: 600;
        font-size: 1.25rem;
}

.style_desig__3Aw4V{
        color: #fda642;
}


